<template>
    <div class="input" :class="jv && 'search'">
        <div class="con">
            <div class="inp" @click="jvGai">
                <i class="iconFont input-icon"></i>
                <input type="text" placeholder="搜索音乐/MV/歌单/歌手" ref="inp" >
            </div>
            <div class="xia" v-show="jv && !val">
                <div class="title">大家都在搜</div>
                <ul>
                    <li v-for="(item, i) in reSou" :key="i">
                        <router-link :to="`/kuwo/search/list?key=${item}`">
                            <span class="list">{{+i + 1}}</span>
                            <span class="text">{{item}}</span>
                        </router-link>
                    </li>
                </ul>
            </div>
            <div class="ti" v-show="jv && val">
                <ul>
                    <li v-for="(item, i) in sou" :key="i">
                        <router-link :to="`/kuwo/search/list?key=${fenXiData(item)}`">
                            <span class="text">{{fenXiData(item)}}</span>
                        </router-link>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import tool from "../static/js/tool"
export default {
    data() {
        return {
            jv: false,
            val: null,
            reSou: [],
            sou: []
        }
    },
    methods: {
        jvGai(e) {
            this.jv = true;
            e.stopPropagation();
        },
        setReSou() {
            tool.ajax('/kuwo/api/www/search/searchKey?key=')
            .then( (data) => {
                this.reSou = data.data;
            } )
        },
        fenXiData(data) {
            let str = data.split(`\n`)[0];
            str = str.split("=")[1];
            if( typeof str !== "string" ) {
                str = "";
            }
            return str.trim();
        }
    },
    computed: {
    },
    mounted() {
        document.addEventListener("click", () => {
            this.jv = false;
        }, false)
        // 获取热搜数据
        this.setReSou();
        // 给input上绑定事件，进行搜索
        this.$refs.inp.oninput = (e) => {
            this.val = e.target.value;
            tool.ajax(`/kuwo/api/www/search/searchKey?key=${this.val}`)
            .then( (data) => {
                this.sou = data.data;
            } )
        }
    }
}
</script>