<template>
    <div class="mv">
        <div class="title">
            <ul>
                <li v-for="(item, i) in title" :key="item.id"
                    :class="i === key ? 'active': ''"
                    @click="link(i)">{{item.title}}</li>
            </ul>
        </div>
        <div class="con">
            <div class="mv_list" v-show="mvBoo">
                <MvList :mvList="mvList"/>
            </div>
            <div class="pag">
                <Paging :index="index" :total="total" :callBack="totalSwitch" />
            </div>
        </div>
    </div>
</template>



<script>
import MvList from "../components/MvList";
import Paging from "../components/Paging.vue";
import tool from "../static/js/tool";
export default {
    data() {
        return {
            title: [
                {
                    id: 236682871,
                    title: '首播'
                },
                {
                    id: 236682731,
                    title: '华语'
                },
                {
                    id: 236742444,
                    title: "日韩"
                },
                {
                    id: 236682773,
                    title: "网络"
                },
                {
                    id: 236682735,
                    title: "欧美"
                },
                {
                    id: 236742576,
                    title: "现场"
                },
                {
                    id: 236682777,
                    title: "热舞"
                },
                {
                    id: 236742508,
                    title: "伤感"
                },
                {
                    id: 236742578,
                    title: '剧情'
                }
            ],
            key: 0,
            // 一页展示多少数据
            rn: 30,

            // 当前的页数
            index: 1,
            // 一共有多少页
            total: 0,
            mvList: [],
            mvBoo: false
        }
    },
    methods: {
        // 获取当前歌手对应的歌曲信息，分页查询
        setMvList(key, pn, rn) {
            // 表示正在申请歌手数据
            this.mvBoo = false;
            // 获取歌手信息，滚动条复位
            window.scrollTo(0, 0);
            var url = `/kuwo/api/www/music/mvList?pid=${key}&pn=${pn}&rn=${rn}`;
            
            tool.ajax(url)
            .then( (data) => {
                this.mvList = data.data.mvlist;
                this.total = parseInt(data.data.total / rn);
                if(this.total * rn < data.data.total) this.total += 1;
                
                // 表示歌手信息已经加装完成
                this.mvBoo = true;
            } )
        },
        // 页码切换
        totalSwitch(total) {
            this.index = total;

            // 页码切换，就要申请对应的歌手数据
            this.setMvList( this.title[this.key].id , this.index, this.rn);
        },
        link(i) {
            this.key = i;
            this.index = 1;
            this.setMvList( this.title[this.key].id , this.index, this.rn);
        }
    },
    mounted () {
        // 加载音乐信息
        this.setMvList( this.title[this.key].id , this.index, this.rn);
    },
    components: {
        Paging,
        MvList
    }
}
</script>