<template>
    <div class="albumList">
        <ul>
            <li v-for="item in albumList" :key="item.albumid"
                @click="link(`/kuwo/album_detail/${item.albumid}`)">
                <div class="img">
                    <img :src="item.pic">
                    <span @click="stopPropagation"><i class="iconFont play-icon" @click="play(item.albumid)"></i></span>
                </div>
                <p class="album daDian">{{item.album}}</p>
                <p class="t">{{item.releaseDate}}</p>
            </li>
        </ul>
    </div>
</template>

<script>
import tool from "../static/js/tool"
export default {
    props: ["albumList"],
    data() {
        return {
            musicAgg: null,
            audio: new Audio()
        }
    },
    methods: {
        play(id) {
            tool.ajax(`/kuwo/api/www/album/albumInfo?albumId=${id}&pn=1&rn=30`)
            .then( (data) => {
                // 取出第一个进行播放
                const id = data.data.musicList[0].rid;
                this.setMusicXin(id);
            } )
        },
        // 获取对应的音乐数据对象
        // 获取歌曲信息
        setMusicXin(id) {
            tool.ajax(`/kuwo/api/www/music/musicInfo?mid=${id}`)
            .then( (data) => {
                this.musicAgg = data.data;
                // 获取对应的音乐路径
                this.musicPlay(id);
            } )
        },
        // 获取音乐
        musicPlay(id) {
            tool.ajax(`/kuwo/url?format=mp3&rid=${id}&response=url&type=convert_url3&br=128kmp3&from=web&t=1587030546976`)
            .then( (data) => {
                const url = data.url;
                this.audio.src = url;
                // 对音乐数据进行保存
                this.baoMusicData(this.musicAgg);

                this.bus.$emit("music", this.musicAgg, this.audio);
            } )
        },
        // 保存音乐数据 
        baoMusicData(musicData) {
            // 读取之前存储过的数据集合
            const data = window.localStorage.getItem('songList');
            if(!data) {
                window.localStorage.setItem('songList', JSON.stringify([musicData]));
            }
            // 已经存在值
            else {
                // 把之前的值转换成数组
                const d = JSON.parse(data);
                for(let i = 0; i < d.length; i ++) {
                    if(musicData.rid == d[i].rid) {
                        d.splice(i, 1);
                        i --;
                    }
                }
                // 把音乐数据添加到开头
                d.unshift(musicData);
                window.localStorage.setItem('songList', JSON.stringify(d));
            }
        },
        // 路径的跳转
        link(url) {
            this.$router.history.push(url);
        },
        stopPropagation(e) {
            e.stopPropagation();
        }
    }
}
</script>