<template>
    <div class="mvList">
        <div class="mvList_con">
            <ul>
                <li v-for="item in mvList" :key="item.id">
                    <router-link :to="`/kuwo/mv/mvplay/${item.id}`">
                        <div class="img">
                            <img :src="item.pic">
                            <i class="iconFont play-icon"></i>
                            <p>
                                <span class="bo">播放量: {{boJiSuan(item.mvPlayCnt)}}</span>
                                <span class="t">{{item.songTimeMinutes}}</span>
                            </p>
                        </div>
                        <p class="name">{{item.name}}</p>
                        <p class="zuo">{{item.artist}}</p>
                    </router-link>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    props: ["mvList"],
    methods: {
        boJiSuan(bo) {
            if(bo >= 1000) {
                let zheng = parseInt(bo / 1000);
                if( zheng * 1000 < bo ) {
                    zheng += 1;
                }
                return zheng / 10 + "万";
            }
            else bo;
        }
    }
}
</script>