<template>
    <div class="mvPlay">
        <div class="content">
            <div class="video">
                <div class="v" :class="quanPingBoo && 'quan'"
                     :style="[sty()]">
                    <video :src="videoSrc" ref="video"></video>
                    <div class="menu">
                        <div class="chongXinPlay" @click="chongXinPlay">
                            <i class="chongXiPlay-icon iconFont"></i>
                        </div>
                        <div class="col_c_p" @click="playQie">
                            <i class="iconFont play-icon" v-show="!playBoo"></i>
                            <i class="iconFont pause-icon" v-show="playBoo"></i>
                        </div>
                        <div class="jinDu">
                            <div class="jin" ref="mv_jin">
                                <div class="time"
                                    :style="`width: ${width}px`"></div>
                                <div class="dian"
                                    :style="`left: ${width}px`"></div>
                            </div>
                            <div class="t">
                                <span>{{dangTime}}</span>/<span>{{dangTimeZhuan(zong)}}</span>
                            </div>
                        </div>
                        <div class="quanPing">
                            <i class="iconFont quanPing-icon"
                               @click="quanPing"></i>
                        </div>
                    </div>
                </div>
                
                <div class="n">
                    <p class="p-1">
                        <span class="name">{{mvXin.name}}</span>
                        -
                        <span class="artist">{{mvXin.artist}}</span>
                    </p>
                    <p class="p-2">播放量: {{mvXin.mvPlayCnt}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import tool from "../static/js/tool";
export default {
    data() {
        return {
            id:0,
            videoSrc: "",
            videoDom: "",
            playBoo: false,
            mvXin: {},

            terval: null,

            // 进度相关的数据
            jinDuChang: 0,
            dangTime: '00:00',
            width: 0,
            zong: 0,
            time: 0,

            quanPingBoo: false,
            html: document.querySelector("html")
        }
    },
    methods: {
        // 获取视频
        getVideoSrc(id) {
            const url = `/kuwo/url?rid=${id}&response=url&format=mp4%7Cmkv&type=convert_url`
            tool.ajax(url)
            .then( (data) => {
                this.videoSrc = data.data;
            } )
        },
        // 获取视频信息
        getMvXin(id) {
            tool.ajax(`/kuwo/api/www/music/musicInfo?mid=${id}`)
            .then( (data) => {
                this.mvXin = data.data;
                // 计算视频的总长度
                this.computedZhong();
            } )
        },
        // 音乐的播放与暂停
        playQie() {
            if(!this.videoSrc) return;
            // 没有播放，变成播放
            if(!this.playBoo) {
                this.playBoo = true;
                this.videoDom.play();
                this.interval();
            }
            // 正在播放，变成停止
            else {
                this.playBoo = false;
                this.videoDom.pause();
                clearInterval(  this.terval );
            }
        },
        // 重新播放视频
        chongXinPlay() {
            // 关掉之前的服务器
            clearInterval(  this.terval );
            // 数据的清空
            this.dangTime = '00:00';
            this.width = 0;
            this.time = 0;
            this.zong = 0;
            
            // 先暂停之前
            this.videoDom.pause();

            this.playBoo = true;
            this.videoDom.currentTime = 0;
            this.videoDom.play();
            this.interval();
        },

        // 启动定时器获取歌曲的进度
        interval() {
            this.terval = setInterval(() => {
                const time = this.videoDom.currentTime;
                //
                this.dangTime = this.dangTimeZhuan(time);
                // 
                this.width = this.computedWidth(time);
                // 获取音乐的总时间
                this.computedZhong();

                // 播放完自动切换
                if(this.time == time && Math.abs(time - this.zong) < 1.5) {
                    // 停止定时器
                    clearInterval(  this.terval );
                }
                else {
                    this.time = time;
                }
            }, 1000 / 60)
        },
        // 当前时间格式的转换
        dangTimeZhuan(t) {
            if(!t) return "00:00";
            let f = parseInt(t / 60);
            let s = parseInt(t - f * 60);
            if(f < 10) f = '0' + f;
            if(s < 10) s = "0" + s;
            return f + ":" + s;
        },
        // 计算时间对应的进度长度
        computedWidth(t) {
            // 总时间与当前时间的比例
            const bi = t / this.zong;
            return parseInt(this.jinDuChang * bi);
        },
        // 计算歌曲的总时间
        computedZhong() {
            // 总时间
            if(this.zong == 0 || !this.zong) {
                this.zong = this.videoDom.duration;
                console.log(this.zong);
            }
        },

        // 全屏处理
        quanPing() {
            if(!this.quanPingBoo) {
                this.html.style.overflow = 'hidden';
                this.html.style.height = window.screen.height + "px";
                this.quanPingBoo = true;
                var dom = document.documentElement;
                dom.requestFullscreen();
                // 重新渲染完后，在计算长度
                setTimeout(() => {
                    this.jinDuChang = parseInt( getComputedStyle(this.$refs.mv_jin).width );
                }, 50)
            }
        },
        sty() {
            if(!this.quanPingBoo) return {
                width: "987px",
                height: "530px"
            }
            return {
                width: window.screen.width + "px",
                height: "811px"
            }
        }
    },
    mounted() {
        this.id = this.$router.history.current.params.id;
        this.videoDom = this.$refs.video;
        this.getVideoSrc( this.id );
        this.getMvXin( this.id );

        // 获取进度条的长度
        this.jinDuChang = parseInt( getComputedStyle(this.$refs.mv_jin).width );

        window.onkeydown = (e) => {
            if(e.keyCode == 27 && this.quanPingBoo) {
                this.quanPingBoo = false;
                this.html.style.overflow = 'auto';
                this.html.style.height = "auto";

                // 重新渲染完后，在计算长度
                setTimeout(() => {
                    this.jinDuChang = parseInt( getComputedStyle(this.$refs.mv_jin).width );
                }, 50)
            }
        }
    }
}
</script>