<template>
    <div class="playlists">
        <div class="title">
            <div class="menu">
                <span>{{type}}</span>
                <i class="xia-icon iconFont" @click="xiaBooGai" v-show="!xiaBoo"></i>
                <i class="shang-icon iconFont" @click="xiaBooGai" v-show="xiaBoo"></i>
            </div>
            <div class="menuS" v-show="type == '精选歌单'">
                <span :class="ty == '最新' && 'active'"
                      @click="zuiXin">最新</span>
                <span :class="ty == '最热' && 'active'"
                      @click="zuiRe">最热</span>
            </div>
            <div class="xia" v-show="xiaBoo">
                <ul class="u">
                    <li class="lei">
                        <div class="title">默认</div>
                        <ul>
                            <li @click="moren" :class="type == '精选歌单' && 'active'">精选歌单</li>
                        </ul>
                    </li>
                    <li v-for="(item, i) in xiaAgg" :key="i" class="lei">
                        <div class="title">{{item.name}}</div>
                        <ul>
                            <li v-for="(item, i) in item.data" :key="i"
                                @click="qieHuan(item.id, item.name)"
                                :class="type == item.name && 'active'">{{item.name}}</li>
                        </ul>
                    </li>
                </ul>
            </div>
        </div>
        <div class="con">
            <PlaylistA :list="list" />
            <Paging :index="index" :total="total" :callBack="totalSwitch" />
        </div>
    </div>
</template>

<script>
import Paging from "../components/Paging.vue";
import PlaylistA from "../components/PlaylistA.vue";
import tool from "../static/js/tool";
export default {
    data() {
        return {
            type: "精选歌单",
            xiaBoo: false,
            id: 0,
            ty: "最热",
            xiaAgg: [],
            list: null,

            // 一页展示多少数据
            rn: 30,

            // 当前的页数
            index: 1,
            // 一共有多少页
            total: 0,
        }
    },
    methods: {
        // 申请下拉菜单的数据
        getXiaAgg() {
            tool.ajax('/kuwo/api/www/playlist/getTagList')
            .then( (data) => {
                this.xiaAgg = data.data;
            } )
        },
        // 改变下拉状态
        xiaBooGai() {
            this.xiaBoo = !this.xiaBoo;
        },
        // 
        qieHuan(id, type) {
            this.id = id;
            this.type = type;
            this.index = 1;
            this.xiaBoo = false;
            // 重新进行歌单的申请
            const url = `/kuwo/api/pc/classify/playlist/getTagPlayList?pn=1&rn=${this.rn}&id=${id}`;
            this.getGeDan(url);
        },
        moren () {
            this.index = 1;
            this.type = '精选歌单';
            this.ty = '最热';
            this.zuiXin();
        },
        // 申请精选歌单对应的最新数据
        zuiXin() {
            if(this.ty == '最热') {
                this.index = 1;
                this.ty = '最新';
                const url = `/kuwo/api/pc/classify/playlist/getRcmPlayList?pn=1&rn=${this.rn}&order=new`;
                this.getGeDan(url);
            }
        },
        zuiRe() {
            if(this.ty == '最新') {
                this.index = 1;
                this.ty = '最热';
                const url = `/kuwo/api/pc/classify/playlist/getRcmPlayList?pn=1&rn=${this.rn}&order=hot`;
                this.getGeDan(url);
            }
        },
        // 申请歌单
        getGeDan(url) {
            tool.ajax(url)
            .then( (data) => {
                this.list = data.data.data;
                this.total = parseInt(data.data.total / this.rn);
                if(this.total * this.rn < data.data.total) this.total += 1;
            } )
        },
        // 页码切换
        totalSwitch(total) {
            this.index = total;
            if(this.type == "精选歌单") {
                if(this.ty == '最新') {
                    const url = `/kuwo/api/pc/classify/playlist/getRcmPlayList?pn=${this.index}&rn=${this.rn}&order=new`;
                }
                else if(this.ty == '最热') {
                    const url = `/kuwo/api/pc/classify/playlist/getRcmPlayList?pn=${this.index}&rn=${this.rn}&order=hot`;
                }

            }
            else {
                const url = `/kuwo/api/pc/classify/playlist/getTagPlayList?pn=${this.index}&rn=${this.rn}&id=${this.id}`;
            }
            // 页码切换，就要申请对应的歌手数据
            this.getGeDan(url);
        }
    },
    mounted () {
        // 申请下拉数据
        this.getXiaAgg();
        // 申请初始菜单
        this.zuiXin();
    },
    components: {
        Paging,
        PlaylistA
    }
}
</script>