<template>
    <div class="search">
        <div class="inp">
            <div class="inp_con">
                <div class="inp_input">
                    <div class="i" @click="jvGai">
                        <input type="text" placeholder="搜索音乐/MV/歌单/歌手" ref="inps">
                        <i class="souSuo-icon iconFont"></i>
                    </div>
                    <div class="xia" v-show="jv && !val">
                        <div class="title">大家都在搜</div>
                        <ul>
                            <li v-for="(item, i) in reSou" :key="i">
                                <router-link :to="`/kuwo/search/list?key=${item}`">
                                    <span class="list">{{+i + 1}}</span>
                                    <span class="text">{{item}}</span>
                                </router-link>
                            </li>
                        </ul>
                    </div>
                    <div class="ti" v-show="jv && val">
                        <ul>
                            <li v-for="(item, i) in sou" :key="i">
                                <router-link :to="`/kuwo/search/list?key=${fenXiData(item)}`">
                                    <span class="text">{{fenXiData(item)}}</span>
                                </router-link>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <div class="tabs">
            <span>搜索结果</span>
            <ul>
                <li><router-link :to='`/kuwo/search/list?key=${val}`'>单曲</router-link></li>
                <li><router-link :to='`/kuwo/search/album?key=${val}`'>专辑</router-link></li>
                <li><router-link :to='`/kuwo/search/mv?key=${val}`'>MV</router-link></li>
                <li><router-link :to='`/kuwo/search/playlist?key=${val}`'>歌单</router-link></li>
                <li><router-link :to='`/kuwo/search/singers?key=${val}`'>歌手</router-link></li>
            </ul>
        </div>
        <router-view />
    </div>
</template>

<script>
import tool from "../static/js/tool"
export default {
    data() {
        return {
            jv: false,
            val: null,
            reSou: [],
            sou: []
        }
    },
    methods: {
        jvGai(e) {
            this.jv = true;
            e.stopPropagation();
        },
        setReSou() {
            tool.ajax('/kuwo/api/www/search/searchKey?key=')
            .then( (data) => {
                this.reSou = data.data;
            } )
        },
        fenXiData(data) {
            let str = data.split(`\n`)[0];
            str = str.split("=")[1];
            if( typeof str !== "string" ) {
                str = "";
            }
            return str.trim();
        }
    },
    computed: {
    },
    mounted() {
        window.addEventListener("click", () => {
            this.jv = false;
        }, false)
        // 获取热搜数据
        this.setReSou();
        // 给input上绑定事件，进行搜索
        this.$refs.inps.oninput = (e) => {
            this.val = e.target.value;
            tool.ajax(`/kuwo/api/www/search/searchKey?key=${this.val}`)
            .then( (data) => {
                this.sou = data.data;
            } )
        }

        // 获取到路径中的初始数据
        this.val = this.$router.history.current.query.key;
        this.$refs.inps.value = this.val;
        if(this.val) {
            tool.ajax(`/kuwo/api/www/search/searchKey?key=${this.val}`)
            .then( (data) => {
                this.sou = data.data;
            } )
        }
    }
}
</script>