import Router from "vue-router";
import Vue from "vue";

import Singers from "../routerComments/Singers.vue";
import Singer_detail from "../routerComments/Singer_detail.vue";
import Singer_detail_list from "../routerComments/Singer_detail_list.vue";
import Singer_detail_mv from "../routerComments/Singer_detail_mv.vue";
import Singer_detail_album from "../routerComments/Singer_detail_album.vue";
import Album_detail from "../routerComments/Album_detail.vue";
import Playlist_detail from "../routerComments/Playlist_detail.vue";

import Play_detail from "../routerComments/Play_detail.vue";
import Content from "../routerComments/Content.vue";
import Ranking from "../routerComments/Ranking.vue";
import Search from "../routerComments/Search.vue";
import Search_list from "../routerComments/Search_list.vue";
import Search_album from '../routerComments/Search_album.vue';
import Search_mv from '../routerComments/Search_Mv.vue';
import Search_playlist from '../routerComments/Search_playlist.vue';
import Search_singers from '../routerComments/Search_singers.vue';
import Recom from '../routerComments/Recom.vue';



import MvPlay from '../routerComments/Mvplay.vue';
import Mv from '../routerComments/Mv.vue';
import Playlists from '../routerComments/Playlists.vue';

Vue.use(Router);

export default new Router ( {
    mode: "history",
    routes: [
        {
            path: "/__kuangJiaKaiFa/vueAppDemo/kuwo/dist/index.html",
            redirect: "/kuwo/tuiJian"
        },
        {
            path: "/project/kuwo",
            redirect: "/kuwo/tuiJian"
        },
        {
            path: "/kuwo",
            redirect: "/kuwo/tuiJian"
        },
        {
            path: "/kuwo/tuiJian",
            name: "content",
            component: Content,
            children: [
                {
                    path: "/kuwo/tuiJian",
                    name: "recom",
                    component: Recom
                },
                {
                    path: "/kuwo/singers",
                    name: "singers",
                    component: Singers,
                },
                {
                    path: "/kuwo/ranking",
                    name: "ranking",
                    component: Ranking,
                },
                {
                    path: "/kuwo/singers/singer_detail",
                    name: "singer_detail",
                    component: Singer_detail, 
                    children: [
                        {
                            path: "/kuwo/singers/singer_detail/list/:id",
                            name: "singer_detail_list",
                            component: Singer_detail_list, 
                        },
                        {
                            path: "/kuwo/singers/singer_detail/mv/:id",
                            name: "singer_detail_mv",
                            component: Singer_detail_mv, 
                        },
                        {
                            path: "/kuwo/singers/singer_detail/album/:id",
                            name: "singer_detail_album",
                            component: Singer_detail_album, 
                        }
                    ]
                },
                {
                    path: "/kuwo/mv",
                    name: "mv",
                    component: Mv
                },
                {
                    path: "/kuwo/mv/mvplay/:id",
                    name: "mvplay",
                    component: MvPlay
                },
                {
                    path: "/kuwo/playlists",
                    name: "/kuwo/playlists",
                    component: Playlists
                }
            ]
        },
        {
            path: "/kuwo/play_detail/:id",
            name: "play_detail",
            component: Play_detail
        },
        {
            path: "/kuwo/search",
            name: "search",
            component: Search,
            children: [
                {
                    path: "/kuwo/search/list",
                    name: "search_list",
                    component: Search_list,
                },
                {
                    path: "/kuwo/search/album",
                    name: "search_album",
                    component: Search_album,
                },
                {
                    path: "/kuwo/search/mv",
                    name: "search_mv",
                    component: Search_mv,
                },
                {
                    path: "/kuwo/search/playlist",
                    name: "search_mv",
                    component: Search_playlist,
                },
                {
                    path: "/kuwo/search/singers",
                    name: "search_mv",
                    component: Search_singers,
                    
                }
            ]
        },
        {
            path: "/kuwo/album_detail/:id",
            name: "album_detail",
            component: Album_detail,
        },
        {
            path: "/kuwo/playlist_detail/:id",
            name: "playlist_detail",
            component: Playlist_detail,
        }
    ]
} )