<template>
    <div class="play">
        <div class="content">
            <div class="d">
                <div class="music">
                    <div class="img">
                        <img :src="musicData && musicData.albumpic" alt="">
                    </div>
                    <div class="jinDu">
                        <div class="xin">
                            <div class="n daDian">
                                <span class="name">{{musicData && musicData.name}}</span>--<span>{{musicData && musicData.artist}}</span>
                            </div>
                            <div class="t">
                                <span>{{dangTime}}</span>/<span>{{musicData && musicData.songTimeMinutes}}</span>
                            </div>
                        </div>
                        <div class="jin" ref="jin">
                            <div class="time"
                                :style="`width: ${width}px`"></div>
                            <div class="dian"
                                :style="`left: ${width}px`"></div>
                        </div>
                    </div>
                </div>
                <div class="menu">
                    <div class="col_c flex_c">
                        <div class="col_c_shang" @click="shang">
                            <i class="iconFont shangYiQu-icon"></i>
                        </div>
                        <div class="col_c_p" @click="playQie">
                            <i class="iconFont play-icon" v-show="!playBoo"></i>
                            <i class="iconFont pause-icon" v-show="playBoo"></i>
                        </div>
                        <div class="col_c_xia" @click="xia">
                            <i class="iconFont xiaYiQu-icon"></i>
                        </div>
                    </div>
                    <div class="col_r flex_c">
                        <div class="xiHuan col_r_i">
                            <i class="iconFont xiHuan-icon"></i>
                        </div>
                        <div class="xiaZai col_r_i">
                            <a :href='`/downMusic/kuwo?musicId=${musicData.rid}`' :download="`${musicData.name}.mp3`">
                                <i class="iconFont xiaZai-icon"></i>
                            </a>
                        </div>
                        <div class="xiaoXi col_r_i">
                            <i class="iconFont xiaoXi-icon"></i>
                        </div>
                        <div class="musicList col_r_i" @click="musicListQie">
                            <i class="iconFont musicList-icon"></i>
                            <span>{{musicAgg.length}}</span>
                        </div>
                        <div class="zhuangTai col_r_i" @click="typeQue">
                            <i class="iconFont zhuangTaiD-icon" v-show="type ==='单曲循环'"></i>
                            <i class="iconFont zhuangTaiX-icon" v-show="type === '顺序播放'"></i>
                        </div>
                        <div class="yinLiang col_r_i">
                            <i class="iconFont yinLiang-icon"></i>
                            <div class="yin"></div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="music_list" v-show="musicListBoo">
                <div class="music_list_title">
                    <div class="p">播放列表<span>(共{{musicAgg.length}}首)</span></div>
                    <div class="qingKong" @click="qingKong">
                        <i class="iconFont huiShou-icon"
                           style="margin-right: 8px;"></i>清空列表
                    </div>
                    <div class="tui" @click="musicListQie">
                        <i class="iconFont shanChu-icon"></i>
                    </div>
                </div>
                <ul>
                    <li v-for="(item, i) in musicAgg" :key="item.rid"
                        :style="[sty(i)]"
                        @click="qieHuan(i)">
                        <div class="x">
                            <span v-show="index != i || !playBoo">{{i + 1}}</span>
                            <span class="iconFont playBoo-icon" v-show="index == i && playBoo"
                                  style="color: #e90e7c"></span>
                        </div>
                        <div class="n" @click="zuZhiMaoPao">
                            <span>{{item.name || " "}}</span>
                            <router-link :to="`/kuwo/mv/mvplay/${item.rid}`" v-show="item.hasmv == 1">MV</router-link>
                        </div>
                        <div class="g">{{item.artist}}</div>
                        <div class="t">{{item.songTimeMinutes}}</div>
                        <div class="shan" @click="zuZhiMaoPao">
                            <i @click="shan(i)" class="iconFont shanChu-icon"></i>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import tool from "../static/js/tool"
export default {
    data() {
        return {
            music: null,
            musicAgg: [],
            musicData: {},
            playBoo: false,
            type: "顺序播放",
            index: 0,
            musicListBoo: false,
            terval: null,
            url: null,

            // 进度相关的数据
            jinDuChang: 0,
            dangTime: '00:00',
            width: 0,
            zong: 0,
            time: 0,

            // 传递当前歌曲播放进度的函数
            timeFun: null
        }
    },
    methods: {
        // 获取音乐
        musicPlay(id) {
            //
            this.music = new Audio();
            tool.ajax(`/kuwo/url?format=mp3&rid=${id}&response=url&type=convert_url3&br=128kmp3&from=web&t=1587030546976`)
            .then( (data) => {
                const url = data.url;
                this.url = url;
                this.music.src = url;
            } )
        },
        // 获取歌曲信息
        setMusicXin() {
            const data = window.localStorage.getItem('songList');
            if(!data) {
                return;
            }
            // 已经存在值
            else {
                // 把之前的值转换成数组，并且进行保存
                this.musicAgg = JSON.parse(data);
                this.musicData = this.musicAgg[0];
            }
        },
        // 音乐的播放与暂停
        playQie() {
            if(!this.music) return;
            // 没有播放，变成播放
            if(!this.playBoo) {
                this.playBoo = true;
                this.music.play();
                // 启动定时器，获取歌曲的进度
                this.interval();
            }
            // 正在播放，变成停止
            else {
                this.playBoo = false;
                this.music.pause();
                // 停止定时器
                clearInterval( this.terval );
            }
        },
        // 把播放状态，变成停止状态，用于音乐的切换
        musicPause() {
            if(this.playBoo) {
                this.playBoo = false;
                this.music.pause();
                clearInterval( this.terval );
            }
        },
        // 下一首
        xia() {
            this.index += 1;
            if( this.index >= this.musicAgg.length ) {
                this.index = 0;
            }
            this.qieHuan(this.index);
        },
        // 上一首
        shang() {
            this.index -= 1;
            if( this.index < 0 ) {
                this.index = this.musicAgg.length - 1;
            }
            this.qieHuan(this.index);
        },
        // 列表的显示与隐藏
        musicListQie() {
            this.musicListBoo = !this.musicListBoo;
        },
        // 切换歌曲
        qieHuan(i) {
            this.timeFun = null;
            this.musicListBoo = false;
            this.index = i;
            // 状态复原
            this.musicPause();
            // 更新音乐数据
            this.musicData = this.musicAgg[ this.index ];
            // 加载对应的音乐
            this.musicPlay(this.musicData.rid);
            // 播放音乐
            this.playQie();
            // 更新歌曲总时间
            this.computedZhong();
        },
        sty(i) {
            const obj = {};
            if( i == this.index ) {
                obj.background = "#f0f0f0";
                obj.color = "#333";
            }
            return obj;
        },
        // 启动定时器获取歌曲的进度
        interval() {
            this.terval = setInterval(() => {
                const time = this.music.currentTime;
                //
                this.dangTime = this.dangTimeZhuan(time);
                // 
                this.width = this.computedWidth(time);

                // 歌曲播放完自动切换
                if(this.time == time && Math.abs(time - this.zong) < 1.5) {
                    if(this.type === "顺序播放") this.xia();
                    else if(this.type === "单曲循环") {
                        this.dangTime = '00:00';
                        this.width = 0;
                        this.time = 0;
                        this.music.play();
                    }
                }
                else {
                    this.time = time;
                }
                if(this.timeFun) this.timeFun(time);
            }, 1000 / 60)
        },
        // 当前时间格式的转换
        dangTimeZhuan(t) {
            if(!t) return "00:00";
            let f = parseInt(t / 60);
            let s = parseInt(t - f * 60);
            if(f < 10) f = '0' + f;
            if(s < 10) s = "0" + s;
            return f + ":" + s;
        },
        // 计算时间对应的进度长度
        computedWidth(t) {
            // 总时间与当前时间的比例
            const bi = t / this.zong;
            return parseInt(this.jinDuChang * bi);
        },
        // 计算歌曲的总时间
        computedZhong() {
            // 总时间
            if(!this.musicData || !this.musicData.songTimeMinutes) {
                this.zhong = 0;
                return;
            }
            const zongArr = this.musicData.songTimeMinutes.split(":");
            this.zong = parseInt(zongArr[0] * 60) + parseInt(zongArr[1]);
        },
        // 状态切换
        typeQue() {
            if(this.type === "单曲循环") {
                this.type = "顺序播放"
            }
            else if(this.type === "顺序播放") {
                this.type = "单曲循环"
            }
        },
        // 组织冒泡
        zuZhiMaoPao(e) {
            e.stopPropagation();
        },
        // 删除列表中的一些数据
        shan(i) {
            this.musicAgg.splice(i, 1);
            window.localStorage.setItem('songList', JSON.stringify(this.musicAgg));
        },
        // 清空音乐列表
        qingKong() {
            this.musicAgg = [];
            window.localStorage.setItem('songList', JSON.stringify(this.musicAgg));

            this.index = 0;
            this.dangTime = '00:00';
            this.width = 0;
            this.time = 0;
            this.musicData = {};
            this.music = null;
            this.musicPause();
        }
    },
    mounted() {
        // 注册事件总线上的函数，用来获取当前播放的歌曲的信息
        this.bus.$on("music", (musicData, music, timeFun) => {
            this.timeFun = timeFun;
            // 状态复原
            this.musicPause();
            this.music = music;
            // 播放音乐
            this.playQie();
            // 保存音乐数据
            this.musicData = musicData;
            this.computedZhong();
            
            for(let i = 0; i < this.musicAgg.length; i ++) {
                if(this.musicAgg[i].rid == musicData.rid) {
                    this.index = i;
                    return;
                }
            }
            
            this.musicAgg.splice(this.index, 0, musicData);
        } );
        // 获取已经播放过的音乐数据
        this.setMusicXin();
        
        if(this.musicData && this.musicData.rid) {
            // 默认加载第一首歌
            this.musicPlay(this.musicData.rid);
        }

        // 获取进度条的长度
        this.jinDuChang = parseInt( getComputedStyle(this.$refs.jin).width );
        // 计算歌曲的总长度
        this.computedZhong();
    }
}
</script>