<template>
    <div class="playlistA">
        <ul>
            <li v-for="item in list" :key="item.id">
                <router-link :to="`/kuwo/playlist_detail/${item.id}`">
                    <div class="img">
                        <img :src="item.img" alt="">
                        <i class="iconFont play-icon"></i>
                    </div>
                    <p class="xin daDian">{{item.name}}</p>
                    <p class="bo">播放量: {{boJiSuan(item.listencnt)}}</p>
                </router-link>
            </li>
        </ul>
    </div>
</template>


<script>
export default {
    props: ['list'],
    methods: {
        boJiSuan(bo) {
            if(bo >= 1000) {
                let zheng = parseInt(bo / 1000);
                if( zheng * 1000 < bo ) {
                    zheng += 1;
                }
                return zheng / 10 + "万";
            }
            else bo;
        }
    }
}
</script>