
<template>
    <div class="album_detail">
        <div class="l">
            <img :src="albumAgg.pic">
            <h1>专辑介绍</h1>
            <p>{{albumAgg.albuminfo}}</p>
        </div>
        <div class="r">
            <div class="xin">
                <h1>{{albumAgg.album}}</h1>
                <p class="artist" @click="link(`/kuwo/singers/singer_detail/list/${albumAgg.artistid}`)">{{albumAgg.artist}}</p>
                <p class="j">
                    <span class="yu">语种: {{albumAgg.lang}}</span>
                    <span>发行时间: {{albumAgg.releaseDate}}</span>
                </p>
            </div>
            <div class="musicList">
                <ul class="title">
                    <li class="head_num">序号</li> 
                    <li class="head_name">歌曲</li> 
                    <li class="head_artist">歌手</li> <!----> 
                    <li class="head_time">时长</li>
                </ul>
                <ul class="list">
                    <li v-for="(item, i) in musicList" :key="item.rid">
                        <router-link :to="`/kuwo/play_detail/${item.rid}`">
                            <span class="head_num">{{ (index - 1) * rn + i }}</span>
                            <span class="head_name daDian">
                                <span>{{item.name || " "}}</span>
                                <router-link :to="`/kuwo/mv/mvplay/${item.rid}`" v-show="item.hasmv == 1">MV</router-link>
                            </span>
                            <span class="head_artist daDian">{{item.artist}}</span>
                            <span class="head_time">{{item.songTimeMinutes}}</span>
                        </router-link>
                    </li>
                </ul>
               <Paging :index="index" :total="total" :callBack="totalSwitch" />
            </div>
        </div>
    </div>
</template>


<script>
import Paging from "../components/Paging.vue";
import tool from "../static/js/tool"
export default {
    data() {
        return {
            id: 0,
            index: 1,
            rn: 30,
            total: 0,

            musicList: null,
            albumAgg: {}
        }
    },
    methods: {
        setMusicList(id, pn, rn) {
            tool.ajax(`/kuwo/api/www/album/albumInfo?albumId=${id}&pn=${pn}&rn=${rn}`)
            .then( (data) => {
                this.musicList = data.data.musicList;
                this.total = parseInt(data.data.total / rn);
                if(this.total * rn < data.data.total) this.total += 1;

                this.albumAgg = data.data;
            } )
        },
        // 页码切换
        totalSwitch(total) {
            this.index = total;

            // 页码切换，就要申请对应的歌手数据
            this.setMusicList(this.id, this.index, this.rn);
        },
        link(url) {
            this.$router.history.push(url);
        }
    },
    mounted() {
        this.id = this.$router.history.current.params.id;
        this.setMusicList(this.id, this.index, this.rn);
    },
    components: {
        Paging
    }
}
</script>