<template>
    <div class="search_list">
        <div class="mus_list" v-show="musicBoo">
            <MusicList :musicList="musicList" width="987" :rn="rn" :index="index" />
        </div>
        <div class="pag">
            <Paging :index="index" :total="total" :callBack="totalSwitch" />
        </div>
    </div>
</template>

<script>
import MusicList from "../components/MusicList";
import Paging from "../components/Paging.vue";
import tool from "../static/js/tool";
export default {
    data() {
        return {
            guan: null,
            // 一页展示多少数据
            rn: 30,

            // 当前的页数
            index: 1,
            // 一共有多少页
            total: 0,
            musicList: [],
            musicBoo: false
        }
    },
    methods: {
        // 获取当前歌手对应的歌曲信息，分页查询
        setMusicAgg(key, pn, rn) {
            // 表示正在申请歌手数据
            this.musicBoo = false;
            // 获取歌手信息，滚动条复位
            window.scrollTo(0, 0);

            var url = `/kuwo/api/www/search/searchMusicBykeyWord?key=${key}&pn=${pn}&rn=${rn}`;
            
            tool.ajax(url)
            .then( (data) => {
                this.musicList = data.data.list;
                this.total = parseInt(data.data.total / rn);
                if(this.total * rn < data.data.total) this.total += 1;
                
                // 表示歌手信息已经加装完成
                this.musicBoo = true;
            } )
        },
        // 页码切换
        totalSwitch(total) {
            this.index = total;

            // 页码切换，就要申请对应的歌手数据
            this.setMusicAgg(this.guan, this.index, this.rn);
        }
    },
    mounted () {
        // 获取搜索关键字
        this.guan = this.$router.history.current.query.key;
        // 加载音乐信息
        this.setMusicAgg(this.guan, this.index, this.rn);
    },
    components: {
        Paging,
        MusicList
    },
    beforeRouteUpdate(to, from, next) {
        next();
        // 获取搜索关键字
        this.guan = this.$router.history.current.query.key;
        // 加载音乐信息
        this.setMusicAgg(this.guan, this.index, this.rn);
    }
}
</script>