<template>
    <div class="Ranking">
        <div class="content clearBoth">
            <div class="con_l">
                <div data-v-2a28ea40="" class="tabs flex_c">
                    <span v-for="(item, i) in rankingAgg" :key="i"
                        :class="typeIndex == i ? 'active': ''"
                        @click="rankingQie(i)">{{item.name}}</span>
                </div>
                <ul data-v-2a28ea40="" class="tab_con">
                    <li class="flex_c" 
                        v-for="(item, i) in ranking" :key="item.id"
                        :class="typeI == i ? 'active': ''"
                        @click="qieHuan(i)">
                        <img data-v-2a28ea40="" alt="" class="cover" :src="item.pic" lazy="loaded"> 
                        <div data-v-2a28ea40="" class="item_info">
                            <p data-v-2a28ea40="" class="name">{{item.name}}</p> 
                            <p data-v-2a28ea40="" class="time">{{item.pub}}</p>
                        </div>
                    </li>
                </ul>
            </div>
            <div class="con_r">
                <div class="con_r_c">
                    <span data-v-2a28ea40="" class="title">{{ranking[typeI] && ranking[typeI].name}}</span> 
                    <span data-v-2a28ea40="" class="update_time">更新时间：{{musicList.pub}}</span>
                </div>
                <div class="list_out">
                    <div data-v-edb94b72="" class="list_head head_name_rank" style="">
                        <ul data-v-edb94b72="" class="flex_c">
                            <li data-v-edb94b72="" class="head_num">序号</li> 
                            <li data-v-edb94b72="" class="head_name">歌曲</li> 
                            <li data-v-edb94b72="" class="head_artist">歌手</li> 
                            <li data-v-edb94b72="" class="head_album">专辑</li> 
                            <li data-v-edb94b72="" class="head_time">时长</li>
                        </ul>
                    </div>
                    <ul class="rank_list" v-show="musicBoo">
                        <li data-v-edb94b72="" class="song_item flex_c"
                            v-for="(item, i) in musicList.musicList" :key="item.rid">
                            <router-link :to="`/kuwo/play_detail/${item.rid}`">
                                <div data-v-edb94b72="" class="song_rank flex_c">
                                    <span>{{(index - 1) * rn + i}}</span>
                                    <img class="cover" :src="item.pic" lazy="loaded">
                                </div> 
                                <div data-v-edb94b72="" class="song_name flex_c">
                                    <span class="name">{{item.name}}</span> 
                                </div> 
                                <div data-v-edb94b72="" class="song_artist">
                                    <span data-v-edb94b72="" title="胡66">{{item.artist}}</span>
                                </div> 
                                <div data-v-edb94b72="" class="song_album">
                                    <span>{{item.album}}</span>
                                </div> 
                                <div data-v-edb94b72="" class="song_time">
                                    <span data-v-edb94b72="">{{item.songTimeMinutes}}</span>
                                </div> 
                            </router-link>
                        </li>
                    </ul>
                    <Paging :index="index" :total="total" :callBack="totalSwitch" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import tool from "../static/js/tool";
import Paging from "../components/Paging";
export default {
    data() {
        return {
            musicList: {
                musicList: []
            },
            typeIndex: 0,
            typeI: 0,

            rankingAgg: [],
            ranking: [],

            // 一页展示多少数据
            rn: 30,

            // 当前的页数
            index: 1,
            // 一共有多少页
            total: 0,
            musicBoo: false
        }
    },
    methods: {
        // 获取到排行榜信息
        setRankingAgg() {
            tool.ajax("/kuwo/api/www/bang/bang/bangMenu")
            .then( (data) => {
                this.rankingAgg = data.data.splice(0, 3);
                this.ranking = this.rankingAgg[ this.typeIndex ].list;
                // 加载初始音乐
                this.setMusicAgg(this.ranking[this.typeI].sourceid , this.index, this.rn);
            } )
        },
        // 更换排行榜的状态
        rankingQie(i) {
            this.ranking = this.rankingAgg[ i ].list;
            this.typeIndex = i;
            this.typeI = 0;
            this.index = 1;
            this.setMusicAgg(this.ranking[this.typeI].sourceid , this.index, this.rn);
        },
        // 获取当前歌手对应的歌曲信息，分页查询
        setMusicAgg(id, pn, rn) {
            this.musicBoo = false;
            // 获取歌手信息，滚动条复位
            window.scrollTo(0, 0);

            var url = `/kuwo/api/www/bang/bang/musicList?bangId=${id}&pn=${pn}&rn=${rn}`;
            
            tool.ajax(url)
            .then( (data) => {
                this.musicList = data.data;
                this.total = parseInt(data.data.num / rn);
                if(this.total * rn < data.data.num) this.total += 1;
                this.musicBoo = true;
            } )
        },
        // 页码切换
        totalSwitch(total) {
            this.index = total;

            // 页码切换，就要申请对应的歌手数据
            this.setMusicAgg(this.ranking[this.typeI].sourceid , this.index, this.rn);
        },

        // 切换排行
        qieHuan(i) {
            this.typeI = i;
            this.index = 1;
            this.setMusicAgg(this.ranking[this.typeI].sourceid , this.index, this.rn);
        }
    },
    mounted() {
        this.setRankingAgg();
    },
    components: {
        Paging
    }
}
</script>