
<template>
    <div class="Recom">
        <div class="content_1">
            <div class="gun">
                <div class="gun_con" :style="`left: -${gunLeft}px; width: ${(gunAgg.length + 1) * 987}px; transition: ${transition}`">
                    <a href="#" v-for="item in gunAgg" :key="item.id">
                        <img :src="item.pic" alt="">
                    </a>
                    <a href="#">
                        <img :src="gunAgg[0].pic">
                    </a>
                </div>
                <i class="iconFont zuo-icon zuo"></i>
                <i class="iconFont you-icon you"></i>
                <p>
                    <span v-for="(item, i) in gunAgg" :key="item.id"
                          :class="dianI == i && 'active'">
                    </span>
                </p>
            </div>
        </div>
        <div class="content_2">
            <div class="title">
                <h4>推荐歌单</h4>
                <ul>
                    <li :class="recomType == '每日推荐' && 'active'"
                        @click="recomClick(0, '每日推荐')">每日推荐</li>
                    <li v-for="item in recomList" :key="item.id"
                        :class="recomType == item.name && 'active'"
                        @click="recomClick(item.id, item.name)">{{item.name}}</li>
                </ul>
            </div>
            <div class="playList">
                <PlaylistA :list="recomData" />
                <div class="pag" v-show="recomBoo">
                    <Paging :index="recomI" :total="total" :callBack="recomTotalSwitch" />
                </div>
            </div>
        </div>
        <div class="content_3">
            <div class="title">
                <h4>排行榜</h4>
            </div>
            <div class="paiHang">
                <ul>
                    <li v-for="item in paiData" :key="item.id">
                        <div class="img">
                            <img :src="item.img1" alt="" class="z">
                            <img :src="item.img2" alt="" class="bei">
                        </div>
                        <div class="list_con">
                            <router-link :to="`/kuwo/play_detail/${item.rid}`" 
                                v-for="(item, i) in item.musicList" :key="item.rid">
                                <div class="list">{{i + 1}}</div>
                                <div class="con">
                                    <p class="name daDian">{{item.name}}</p>
                                    <p class="zuo daDian">{{item.artist}}</p>
                                </div>
                            </router-link>
                        </div>
                    </li>
                </ul>
                
            </div>
        </div>
        <div class="content_4">
            <div class="title">
                <h4>歌手推荐</h4>
                <ul>
                    <li v-for="item in artistList" :key="item.id"
                        :class="recomType == item.name && 'active'"
                        @click="getSingersList(item.id, 1, artistRn)">{{item.name}}</li>
                </ul>
            </div>
            <div class="artistList">
                <SingersList :singersList="singersList" />
                <div class="pag">
                    <Paging :index="artistIndex" :total="artistTotal" :callBack="artistSwitch" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import PlaylistA from "../components/PlaylistA.vue";
import SingersList from "../components/SingersList.vue";
import Paging from "../components/Paging.vue";
import tool from "../static/js/tool";
export default {
    data() {
        return {
            gunAgg: null,
            gunVal: null,
            gunI: 0,
            gunLeft: 0,
            transition: "all 1s linear",
            dianI: 0,

            // 每日推荐的菜单表
            recomList: null,
            recomType: null,
            recomData: null,
            recomBoo: false,
            recomI: 1,
            rn: 5,
            total: 0,
            recomId: 0,

            // 歌手推荐
            artistList: null,
            singersList: null,
            artistIndex: 1,
            artistRn: 5,
            artistTotal: 0,
            artistId: 0,
            
            // 排行榜
            paiData: null

        }
    },
    methods: {
        // 获取轮播图片的数据请求
        getLunBoAgg() {
            tool.ajax('/kuwo/api/www/banner/index/bannerList')
            .then( (data) => {
                this.gunAgg = data.data;
                // 启动定时器进行轮播
                this.terval();
            } )
        },
        // 开启定时器，进行轮播
        terval() {
            this.gunVal = setInterval(() => {
                this.gunI ++;
                this.gunLeft = this.gunI * 987;

                // 滚动到最后一张连接处了
                if( this.gunI > this.gunAgg.length ) {
                    this.transition = "none";
                    this.gunLeft = 0;
                    this.gunI = 1;
                    setTimeout( () => {
                        this.transition = "all 1s linear";
                        this.gunLeft = this.gunI * 987;
                    }, 50)
                }

                setTimeout( () => {
                    // 计算原点的索引，滚动完在进行计算
                    this.dianI = this.gunI;
                    if( this.gunI >= this.gunAgg.length ) {
                        this.dianI = 0;
                    }
                }, 1000)

            }, 4000)
        },

        // 申请每日推荐的菜单表
        getRecomList() {
            tool.ajax('/kuwo/api/www/playlist/index/tags')
            .then( (data) => {
                this.recomList = data.data;
            } )
        },
        // 申请推荐歌单对应的歌单数据
        recomClick(id, name) {
            if(this.recomType == name) {
                return;
            }
            this.recomI = 1;
            this.recomType = name;
            if(name == '每日推荐') {
                tool.ajax('/kuwo/api/www/rcm/index/playlist?loginUid=1824662025&loginSid=3250285219')
                .then( (data) => {
                    this.recomData = data.data.list.splice(0, 5);
                } )
                this.recomBoo = false;
            }
            else {
                this.recomId = id;
                tool.ajax(`/kuwo/api/pc/classify/playlist/getTagPlayList?loginUid=1824662025&loginSid=3250285219&id=${id}&pn=${this.recomI}&rn=${this.rn}`)
                .then( (data) => {
                    this.recomData = data.data.data;

                    this.total = parseInt(data.data.total / this.rn);
                    if(this.total * this.rn < data.data.total) this.total += 1;
                } )
                this.recomBoo = true;
            }
        },
        // 页码切换
        recomTotalSwitch(total) {
            this.recomI = total;

            // 页码切换，就要申请对应的歌手数据
            tool.ajax(`/kuwo/api/pc/classify/playlist/getTagPlayList?loginUid=1824662025&loginSid=3250285219&id=${this.recomId}&pn=${this.recomI}&rn=${this.rn}`)
            .then( (data) => {
                this.recomData = data.data.data;

                this.total = parseInt(data.data.total / this.rn);
                if(this.total * this.rn < data.data.total) this.total += 1;
            } )
            this.recomBoo = true;
        },


        // 申请歌手推荐的菜单表
        getArtistList() {
            tool.ajax('/kuwo/api/www/artist/index/tags')
            .then( (data) => {
                this.artistList = data.data;
                this.artistId = this.artistList[0].id;
                // 申请歌手推荐的信息数据
                this.getSingersList(this.artistId, 1, this.artistRn)
            } )
        },
        // 申请歌手推荐的信息数据
        getSingersList(id, i, rn) {
            this.artistIndex = i;
            this.artistId = id;
            tool.ajax(`/kuwo/api/www/artist/artistInfo?category=${id}&pn=${i}&rn=${rn}`)
            .then( (data) => {
                this.singersList = data.data.artistList;

                this.artistTotal = parseInt(data.data.total / rn);
                if(this.artistTotal * rn < data.data.total) this.artistTotal += 1;
            } )
        },
        // 页面切换
        artistSwitch(total) {
            this.getSingersList(this.artistId, total, this.artistRn)
        },

        // 申请排行榜数据
        getPaiData() {
            tool.ajax('/kuwo/api/www/bang/index/bangList')
            .then( (data) => {
                this.paiData = data.data;
                this.paiData[0].img1 = 'https://h5static.kuwo.cn/upload/image/d8faf807ce667092ef29f8d62237bfbf3a1f127a6370664705722a67c8f9e23b.png';
                this.paiData[0].img2 = 'http://img3.kuwo.cn/star/albumcover/300/56/21/2954495527.jpg';

                this.paiData[1].img1 = 'https://h5static.kuwo.cn/upload/image/fb29b783091117318dbb5dac53f8a7deee25b34054f7d00fb91640f6106773c6.png';
                this.paiData[1].img2 = 'http://img3.kuwo.cn/star/albumcover/300/2/99/1477081848.jpg';

                this.paiData[2].img1 = 'https://h5static.kuwo.cn/upload/image/fcd292e95c97704678ae6a77191df0b915dd9b9657296ca20b9f19047f672055.png';
                this.paiData[2].img2 = 'http://img1.kuwo.cn/star/albumcover/300/19/94/3268290676.jpg';

                this.paiData[3].img1 = 'https://h5static.kuwo.cn/upload/image/8eef6e95406eb46f8ed544384bb457e20c8d66a33c3077c586e58537b620ecf5.png';
                this.paiData[3].img2 = 'http://img4.kuwo.cn/star/albumcover/300/79/6/2165672716.jpg';

                this.paiData[4].img1 = 'https://h5static.kuwo.cn/upload/image/838f7519b40479695a6f5a22923704600d2dfceb4e8d6ad0099b5a169ae0df03.png';
                this.paiData[4].img2 = 'http://img4.kuwo.cn/star/albumcover/300/42/81/546622733.jpg';
            } )
        }
    },
    mounted() {
        // 申请每日推荐
        this.getLunBoAgg();

        // 获取每日推荐的菜单表
        this.getRecomList();
        // 申请每日推荐的初始数据
        this.recomClick(0, '每日推荐');

        // 申请歌手推荐的菜单表
        this.getArtistList();

        // 申请排行榜的数据
        this.getPaiData();
    },
    beforeDestroy() {
        clearInterval(this.gunVal);
    },
    components: {
        PlaylistA,
        SingersList,
        Paging
    }
}
</script>