<template>
    <div class="singer_detail">
        <div class="singer_info">
            <div class="content">
                <div class="s_imgout">
                    <img :src="singerAgg.pic300">
                </div>
                <div class="info"> 
                    <p data-v-5fbff215="" class="name_out flex_c">
                        <span data-v-5fbff215="" class="name">{{singerAgg.name}}</span>
                    </p> 
                    <div data-v-5fbff215="" class="info_items">
                        <span data-v-5fbff215="">单曲：
                            <span data-v-5fbff215="" class="num">{{singerAgg.musicNum}}</span>
                        </span> 
                        <span data-v-5fbff215="">专辑：
                            <span data-v-5fbff215="" class="num">{{singerAgg.albumNum}}</span>
                        </span> 
                        <span data-v-5fbff215="">MV：
                            <span data-v-5fbff215="" class="num">{{singerAgg.mvNum}}</span>
                        </span> 
                        <span data-v-5fbff215="">粉丝：
                            <span data-v-5fbff215="" class="num">
                                {{singerAgg.artistFans >= 10000 ? parseInt(singerAgg.artistFans / 1000) / 10 + "W" : singerAgg.artistFans}}
                            </span>
                        </span>
                    </div> 
                    <div data-v-5fbff215="" class="singer_items flex_c">
                        <p data-v-5fbff215="">
                            <span data-v-5fbff215="">英文名：
                                <span data-v-5fbff215="">-</span>
                            </span> 
                            <span data-v-5fbff215="">国籍：
                                <span data-v-5fbff215="">{{singerAgg.country}}</span>
                            </span> 
                            <span data-v-5fbff215="">语言：
                                <span data-v-5fbff215="">{{singerAgg.language}}</span>
                            </span> 
                            <span data-v-5fbff215="">出生地：
                                <span data-v-5fbff215="">{{singerAgg.birthplace}}</span>
                            </span> 
                            <span data-v-5fbff215="">星座：
                                <span data-v-5fbff215="">-...</span>
                            </span>
                        </p> 
                        <span data-v-5fbff215="" class="all">全部 &gt;</span>
                    </div> 
                    <div data-v-5fbff215="" class="btns">
                        <button data-v-5fbff215="" class="play" style="">播放全部歌曲</button>
                        <button data-v-5fbff215="">收藏</button>
                        <button data-v-5fbff215="">使用客户端查看歌手</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="main_con">
            <div class="content">
                <div class="content_left">
                    <div class="tabs flex_c">
                        <router-link :to="`/kuwo/singers/singer_detail/list/${id}`">单曲</router-link>
                        <router-link :to="`/kuwo/singers/singer_detail/album/${id}`">专辑</router-link>
                        <router-link :to="`/kuwo/singers/singer_detail/mv/${id}`">MV</router-link>
                        <router-link to="#">简介</router-link>
                    </div>
                    <div class="music">
                        <router-view />
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import tool from "../static/js/tool";
export default {
    data() {
        return  {
            id: 0,
            // 对应的歌手信息
            singerAgg: {},
        }
    },
    mounted() {
        this.id = this.$router.history.current.params.id;
        // 申请歌手数据
        this.setSingerAgg(this.id);
    },
    methods: {
        // 获取当前歌手对应的基本信息
        setSingerAgg(id) {
            var url = `/kuwo/api/www/artist/artist?artistid=${id}`;
            tool.ajax(url)
            .then( (data) => {
                this.singerAgg = data.data;
            } )
        }
    }
}

</script>


