<template>
    <div class="search_album">
        <div class="con" v-show="albumBoo">
            <AlbumList :albumList="albumList"/>
        </div>
        <div class="pag">
            <Paging :index="index" :total="total" :callBack="totalSwitch" />
        </div>
    </div>
</template>

<script>
// 
import AlbumList from "../components/AlbumList.vue";
import Paging from "../components/Paging.vue";
import tool from "../static/js/tool";
export default {
    data() {
        return {
            guan: null,
            // 一页展示多少数据
            rn: 30,

            // 当前的页数
            index: 1,
            // 一共有多少页
            total: 0,
            albumList: [],
            albumBoo: false
        }
    },
    methods: {
        // 获取当前歌手对应的歌曲信息，分页查询
        setAlbumList(key, pn, rn) {
            // 表示正在申请歌手数据
            this.albumBoo = false;
            // 获取歌手信息，滚动条复位
            window.scrollTo(0, 0);

            var url = `/kuwo/api/www/search/searchAlbumBykeyWord?key=${key}&pn=${pn}&rn=${rn}`;
            
            tool.ajax(url)
            .then( (data) => {
                console.log(data);
                this.albumList = data.data.albumList;
                this.total = parseInt(data.data.total / rn);
                if(this.total * rn < data.data.total) this.total += 1;
                
                // 表示歌手信息已经加装完成
                this.albumBoo = true;
            } )
        },
        // 页码切换
        totalSwitch(total) {
            this.index = total;

            // 页码切换，就要申请对应的歌手数据
            this.setAlbumList(this.guan, this.index, this.rn);
        }
    },
    mounted () {
        // 获取搜索关键字
        this.guan = this.$router.history.current.query.key;
        // 加载音乐信息
        this.setAlbumList(this.guan, this.index, this.rn);
    },
    components: {
        Paging,
        AlbumList
    },
    beforeRouteUpdate(to, from, next) {
        next();
        // 获取搜索关键字
        this.guan = this.$router.history.current.query.key;
        // 加载音乐信息
        this.setAlbumList(this.guan, this.index, this.rn);
    }
}
</script>