<template>
    <div class="search_playlist">
        <div class="con">
            <SingersList :singersList="singersList" />
        </div>
        <div class="pag">
            <Paging :index="index" :total="total" :callBack="totalSwitch" />
        </div>
    </div>
</template>

<script>
import SingersList from "../components/SingersList";
import Paging from "../components/Paging.vue";
import tool from "../static/js/tool";
export default {
    data() {
        return {
            guan: null,
            // 一页展示多少数据
            rn: 30,

            // 当前的页数
            index: 1,
            // 一共有多少页
            total: 0,

            singersList: []
        }
    },
    methods: {
        // 获取当前歌手对应的歌曲信息，分页查询
        setSingersList(key, pn, rn) {
            // 获取歌手信息，滚动条复位
            window.scrollTo(0, 0);
            
            var url = `/kuwo/api/www/search/searchArtistBykeyWord?key=${key}&pn=${pn}&rn=${rn}`;
            
            tool.ajax(url)
            .then( (data) => {
                this.singersList = data.data.list;

                this.total = parseInt(data.data.total / rn);
                if(this.total * rn < data.data.total) this.total += 1;
                
            } )
        },
        // 页码切换
        totalSwitch(total) {
            this.index = total;

            // 页码切换，就要申请对应的歌手数据
            this.setSingersList(this.guan, this.index, this.rn);
        }
    },
    mounted () {
        // 获取搜索关键字
        this.guan = this.$router.history.current.query.key;
        // 加载音乐信息
        this.setSingersList(this.guan, this.index, this.rn);
    },
    components: {
        Paging,
        SingersList
    },
    beforeRouteUpdate(to, from, next) {
        next();
        // 获取搜索关键字
        this.guan = this.$router.history.current.query.key;
        // 加载音乐信息
        this.setSingersList(this.guan, this.index, this.rn);
    }
}
</script>