<template>
    <div class="paging" v-if="boo">
        <ul>
            <li @click="shang">
                <i class="zuo-icon iconFont"></i>
            </li>
            <li v-for="(item, i) in computedPag" :key="i"
                :class="index == item ? 'active' : ''"
                @click="cli(item)">{{item}}</li>
            <li @click="xia">
                <i class="you-icon iconFont"></i>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: ["total", "index", "callBack"],
    data() {
        return {
            boo: true,
        }
    },
    computed: {
        computedPag() {
            let pageAgg = [];
            if( this.total <= 1 ) this.boo = false;
            else if(this.total <= 7) {
                for(let i = 0; i < this.total; i ++) {
                    pageAgg.push(i + 1);
                }
            }
            else if(this.index <= 3 && this.total > 7) {
                pageAgg = [1, 2, 3, '···'];
                pageAgg.push(this.total);
            }
            else if(this.index == 4 && this.total > 7) {
                pageAgg = [1, 2, 3, 4, '···'];
                pageAgg.push(this.total);
            }
            else if(this.index > 4 && this.index < this.total - 3 && this.total > 7) {
                pageAgg = [1, '···', this.index - 1, this.index, this.index + 1, '···'];
                pageAgg.push(this.total);
            }
            else if(this.index == this.total - 3 && this.total > 7) {
                pageAgg = [1, '···', this.total - 3, this.total - 2, this.total - 1];
                pageAgg.push(this.total);
            }
            else if(this.index >= this.total - 2 && this.total > 7) {
                pageAgg = [1, '···', this.total - 2, this.total - 1];
                pageAgg.push(this.total);
            }
            
            this.boo = true;

            return pageAgg;
        }
    },
    methods: {
        shang() {
            let i = this.index;
            i --;
            if(i < 1) {
                return;
            }
            this.callBack( i );
        },
        xia() {
            let i = this.index;
            i ++;
            if(i > this.total) {
                return;
            }
            this.callBack( i );
        },
        cli(i) {
            if(i == "···") return;
            this.callBack( i );
        }
    }
}
</script>
