
<template>
    <div class="playList_detail">
        <div class="l">
            <img :src="playListAgg.img500">
            <h1>歌单简介</h1>
            <p>{{playListAgg.info}}</p>
        </div>
        <div class="r">
            <div class="xin">
                <h1>{{playListAgg.name}}</h1>
                <p class="artist">
                    <img :src="playListAgg.uPic" alt="">
                    <span>{{playListAgg.userName}}</span>
                </p>
                <p class="j">
                    {{playListAgg.tag}}
                </p>
            </div>
            <div class="musicList">
                <ul class="title">
                    <li class="head_num">序号</li> 
                    <li class="head_name">歌曲</li> 
                    <li class="head_artist">歌手</li> <!----> 
                    <li class="head_time">时长</li>
                </ul>
                <ul class="list">
                    <li v-for="(item, i) in playList" :key="item.rid">
                        <router-link :to="`/kuwo/play_detail/${item.rid}`">
                            <span class="head_num">{{ (index - 1) * rn + i }}</span>
                            <span class="head_name daDian">
                                <span>{{item.name || " "}}</span>
                                <router-link :to="`/kuwo/mv/mvplay/${item.rid}`" v-show="item.hasmv == 1">MV</router-link>
                            </span>
                            <span class="head_artist daDian">{{item.artist}}</span>
                            <span class="head_time">{{item.songTimeMinutes}}</span>
                        </router-link>
                    </li>
                </ul>
               <Paging :index="index" :total="total" :callBack="totalSwitch" />
            </div>
        </div>
    </div>
</template>


<script>
import Paging from "../components/Paging.vue";
import tool from "../static/js/tool"
export default {
    data() {
        return {
            id: 0,
            index: 1,
            rn: 30,
            total: 0,

            playList: null,
            playListAgg: {}
        }
    },
    methods: {
        setPlayList(id, pn, rn) {
            tool.ajax(`/kuwo/api/www/playlist/playListInfo?pid=${id}&pn=${pn}&rn=${rn}`)
            .then( (data) => {
                this.playList = data.data.musicList;
                this.total = parseInt(data.data.total / rn);
                if(this.total * rn < data.data.total) this.total += 1;

                this.playListAgg = data.data;
            } )
        },
        // 页码切换
        totalSwitch(total) {
            this.index = total;

            // 页码切换，就要申请对应的歌手数据
            this.setPlayList(this.id, this.index, this.rn);
        }
    },
    mounted() {
        this.id = this.$router.history.current.params.id;
        this.setPlayList(this.id, this.index, this.rn);
    },
    components: {
        Paging
    }
}
</script>